import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";

class FormGeneralInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
  }

  render() {
    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Site Power Topology"
              />
                          
            </div>
          </Grid>

          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Site Security"
              />
             
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

export default FormGeneralInfo;
