import { UPDATE_EXPENSE } from '../constants/action-types';
import { UPDATE_EXCEL } from '../constants/action-types';
import {
    FORM_SITE_IDENTIFICATION,
    FORM_SAQ_SITE,
    FORM_TOWER_INFO,
    FORM_CIVIL_INFO,
    FORM_GENERATOR_INFO,
    FORM_GRID_POWER_INFO,
    FORM_EQUIPMENT_INFO,
    FORM_POWER_READING,
} from '../constants/action-types';

const initialstate = {
    currentMonth: {
        drainage: "2000",
        water: "12000",
        security: "22000",
        electricity: "50010"
    },
    excelPreview: {
        rows: "",
        column: ""
    },
    siteID: {},
    saqSite: {},
    towerInfo: {},
    civilInfo: {},
    generatorInfo: {},
    gridPowerInfo: {},
    equipmentInfo: {},
    powerReading: {},
}
function rootReducer(state = initialstate, action) {

    let newState = {};

    switch (action.type) {
        case UPDATE_EXPENSE:
            newState = { currentMonth: action.payload }
            break;

        case UPDATE_EXCEL:
            newState = { excelPreview: action.payload }
            break;

        case FORM_SITE_IDENTIFICATION:
            newState = { siteID: action.payload }
            break;

        case FORM_SAQ_SITE:
            newState = { saqSite: action.payload }
            break;

        case FORM_TOWER_INFO:
            newState = { towerInfo: action.payload }
            break;

        case FORM_CIVIL_INFO:
            newState = { civilInfo: action.payload }
            break;

        case FORM_GENERATOR_INFO:
            newState = { generatorInfo: action.payload }
            break;

        case FORM_GRID_POWER_INFO:
            newState = { gridPowerInfo: action.payload }
            break;

        case FORM_EQUIPMENT_INFO:
            newState = { equipmentInfo: action.payload }
            break;

        case FORM_POWER_READING:
            newState = { powerReading: action.payload }
            break;

        default:
            break;
    }

    return Object.assign({}, state, newState);
}

export default rootReducer;