import React, { Component, Fragment } from "react";
import {
  IconButton,
  LinearProgress,
  Grid,
  Card,
  CardContent,
  Button,
  Divider,
  Tooltip,
} from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { connect } from "react-redux";

import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Collapse from "@material-ui/core/Collapse";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
import RefreshIcon from "@material-ui/icons/Refresh";
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { Link } from "react-router-dom";
const rows = require('../config/wodetails.json');


const useRowStyles = makeStyles({
  root: {
    "& > *": {
      borderBottom: "unset",
    },
  },
});

class CollapsableTable extends Component {
  constructor(props) {
    super(props);
    let obj = [];
    if (props.serviceAssignmentRows) {
      props.serviceAssignmentRows.map((row, index) => {
        obj[index] = row.assignee;
      });
    }
    this.state = {
      open: false,
      assignee: obj,
      displaynone: true,
      displayAssignee: true
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleOnClick = this.handleOnClick.bind(this);
  }
  handleOnClick = () => {
    this.setState({
      displaynone: false,
      displayAssignee: false
    });
  };
  handleChange = index => (event) => {
    let assignee = [...this.state.assignee];
    assignee[index] = event.target.value;
    this.setState({ assignee });
    this.setState({
      displaynone: true,
      displayAssignee: true
    });
  };

  render() {

    return (

      <Fragment>
        {this.props.serviceAssignmentRows ? (
          <Box margin={3}>
            <h8>
              <strong>Service Assignment</strong>
            </h8>
            <div className="table-responsive"></div>
            <Table className="table table-borderless table-hover text-nowrap mb-0">
              <TableHead>
                <TableRow>
                  <th className="text-center">SITE ID</th>
                  <th className="text-center">ASSIGNEE</th>
                  <th className="text-center">COLO PARTNER</th>
                  <th className="text-center">SCOPE</th>
                  <th className="text-center">VENDOR</th>

                  <th className="text-center">STATE</th>
                  <th className="text-center">status</th>
                  <th className="text-center">assigneddate</th>
                  <th className="text-center">Site details</th>
                </TableRow>
              </TableHead>
              <tbody>

                {this.props.serviceAssignmentRows.map((row, index) => (
                  <tr>
                    <td className="text-center">
                      <div className="font-weight-normal text-black">
                        <Link to="/profile">{row.siteid}</Link>

                      </div>
                    </td>
                    <td className="text-center">
                      <div className="font-weight-normal  text-black ">

                        <span className={this.state.displaynone ? "display-none" : ""}>
                          <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={this.state.assignee[index]}
                            onChange={this.handleChange(index)}
                          >
                            <MenuItem value={'John Michael'}>John Michael</MenuItem>
                            <MenuItem value={'Morkel Styne'}>Morkel Styne</MenuItem>
                            <MenuItem value={'Mike David'}>Mike David</MenuItem>
                            <MenuItem value={'Mike Swain'}>Mike Swain</MenuItem>
                          </Select>
                        </span>
                        <span className={this.state.displayAssignee ? "font-weight-normal  text-black" : "display-none"}
                        >
                          {this.state.assignee[index]}
                        </span>

                        <span class="material-icons MuiIcon-root icon-size" aria-hidden="true" onClick={this.handleOnClick}>edit</span>
                      </div>
                    </td>
                    <td className="text-center">
                      <div className="font-weight-normal text-black">
                        {row.colopartner}
                      </div>
                    </td>
                    <td className="text-center">
                      <div className="font-weight-normal text-black">
                        {row.scope}
                      </div>
                    </td>
                    <td className="text-center">
                      <div className="font-weight-normal text-black">
                        {row.vendor}
                      </div>
                    </td>

                    <td className="text-center">
                      <div className="font-weight-normal text-black">
                        {row.state}
                      </div>
                    </td>
                    <td className="text-center">
                      <div className="badge badge-warning px-4">
                        {row.status}
                      </div>
                    </td>
                    <td className="text-center">
                      <div className="font-weight-normal text-black">
                        {row.assigneddate}
                      </div>
                    </td>
                    <td className="text-center">
                      <Button variant="outlined" color="primary" href="#outlined-buttons">
                        <Link to="/siteDetails">View</Link>
                      </Button>
                    </td>
                  </tr>))}
              </tbody>
            </Table>
          </Box>
        ) : ''}
      </Fragment >
    );
  }
}

export default CollapsableTable;
