import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { civilInfoAction } from "../actions/SurveyActions";

class FormCivilInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        CI_Site_Raise: "",
        CI_Foundation_Type: "",
        CI_Civil_Foundation: "",
        CI_Tie_Beam_Type: "",
        CI_Plinth_DG: "",
        CI_Plinth_Equipments: "",
        CI_Fencing_Type: "",
        CI_Palisade_Fence: "",
        CI_Fencing_Dimension: "",
        CI_Site_Earthing: "",
        CI_Number_Earth_pits: "",
        CI_Earth_Reading: "",
        CI_Guard_House_Exists: "",
        CI_Manhole_Exists: "",
        CI_No_of_Manholes: "",
        CI_Locking_Mechanism: "",
        CI_Razor_Coil_Exists: "",
        CI_Site_Signages_Exists: "",
        CI_Obstruction_nearby: "",
        CI_OFC_distance: "",
        CI_Access_Road_Exists: "",
      }
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.civilInfo });
  }

  render() {
    const { civilInfoDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      civilInfoDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Site Raise (cm)"
                id="CI_Site_Raise"
                onChange={onChangeValue}
                value={this.state.formData.CI_Site_Raise}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Foundation Type (KPA)"
                id="CI_Foundation_Type"
                onChange={onChangeValue}
                value={this.state.formData.CI_Foundation_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Civil Foundation for Tower"
                id="CI_Civil_Foundation"
                onChange={onChangeValue}
                value={this.state.formData.CI_Civil_Foundation}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Tie Beam Type"
                id="CI_Tie_Beam_Type"
                onChange={onChangeValue}
                value={this.state.formData.CI_Tie_Beam_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Plinth For DG"
                id="CI_Plinth_DG"
                onChange={onChangeValue}
                value={this.state.formData.CI_Plinth_DG}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Plinth for Equipments"
                id="CI_Plinth_Equipments"
                onChange={onChangeValue}
                value={this.state.formData.CI_Plinth_Equipments}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Fencing Type"
                id="CI_Fencing_Type"
                onChange={onChangeValue}
                value={this.state.formData.CI_Fencing_Type}
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Palisade Fence"
                id="CI_Palisade_Fence"
                onChange={onChangeValue}
                value={this.state.formData.CI_Palisade_Fence}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Fencing dimension ( HtxLxB) "
                id="CI_Fencing_Dimension"
                onChange={onChangeValue}
                value={this.state.formData.CI_Fencing_Dimension}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Earthing"
                id="CI_Site_Earthing"
                onChange={onChangeValue}
                value={this.state.formData.CI_Site_Earthing}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Number of Earth pits"
                id="CI_Number_Earth_pits"
                onChange={onChangeValue}
                value={this.state.formData.CI_Number_Earth_pits}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Earth Reading (Ohms)"
                id="CI_Earth_Reading"
                onChange={onChangeValue}
                value={this.state.formData.CI_Earth_Reading}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Guard House"
                id="CI_Guard_House_Exists"
                onChange={onChangeValue}
                value={this.state.formData.CI_Guard_House_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Manhole"
                id="CI_Manhole_Exists"
                onChange={onChangeValue}
                value={this.state.formData.CI_Manhole_Exists}
              />

            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="No of Manholes"
                id="CI_No_of_Manholes"
                onChange={onChangeValue}
                value={this.state.formData.CI_No_of_Manholes}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Locking Mechanism "
                id="CI_Locking_Mechanism"
                onChange={onChangeValue}
                value={this.state.formData.CI_Locking_Mechanism}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Razor Coil"
                id="CI_Razor_Coil_Exists"
                onChange={onChangeValue}
                value={this.state.formData.CI_Razor_Coil_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Signages"
                id="CI_Site_Signages_Exists"
                onChange={onChangeValue}
                value={this.state.formData.CI_Site_Signages_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Obstruction nearby"
                id="CI_Obstruction_nearby"
                onChange={onChangeValue}
                value={this.state.formData.CI_Obstruction_nearby}
              />
              <TextField
                fullWidth
                className="m-2"
                label="OFC distance"
                id="CI_OFC_distance"
                onChange={onChangeValue}
                value={this.state.formData.CI_OFC_distance}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Access Road"
                id="CI_Access_Road_Exists"
                onChange={onChangeValue}
                value={this.state.formData.CI_Access_Road_Exists}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    civilInfo: state.civilInfo,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    civilInfoDispatch: (data) => dispatch(civilInfoAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormCivilInfo);