import React, { Component, Fragment } from "react";
import {TextField,Grid} from "@material-ui/core";

class FormExternalAlarms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
  }

  render() {
    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Mains Failure Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Rectifier Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Gen-1 Failure Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Gen-2 Failure Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="High Temperature Alarm"
              />
              
            </div>
          </Grid>

          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Door Open Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Fuel Level Low Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="AC-1 Failure Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="AC-2 Failure Alarm"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Fire and Smoke Alarm"
              />
             
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

export default FormExternalAlarms;

