import React, { Component, Fragment } from "react";
import clsx from "clsx";
import { ListItem, Button, Collapse } from '@material-ui/core';
import { NavLink } from "react-router-dom";
import HomeIcon from '@material-ui/icons/Home';
import AddToQueueIcon from '@material-ui/icons/AddToQueue';
import DashboardIcon from '@material-ui/icons/Dashboard';
import AddAlertIcon from '@material-ui/icons/AddAlert';

let paddingLeft = 22;
const style = {
    paddingLeft
};
const divStyle = {
    "font-size": "16px",
    color: "white",
    display: "inline-block"
};
class SidePanelMenu extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <ListItem
                className={clsx('app-sidebar-item')}
                style={{ "margin-top": "40px" }}
                disableGutters>
                <Button
                    color="primary"
                    className={clsx('app-sidebar-button')}
                    style={style}>
                    <NavLink to="/">
                        <DashboardIcon style={{ fontSize: 30, color: "white" }} /> &nbsp;
                        <p style={divStyle}>Dashboard</p>
                    </NavLink>
                </Button>
                <Button
                    color="primary"
                    className={clsx('app-sidebar-button')}
                    style={style}>
                    <NavLink to="/profile">
                        <AddToQueueIcon style={{ fontSize: 30, color: "white" }} /> &nbsp;
                        <p style={divStyle}>Create Work Order</p>
                    </NavLink>
                </Button>
                <Button
                    color="primary"
                    className={clsx('app-sidebar-button')}
                    style={style}>
                    <NavLink to="/">
                        <AddAlertIcon style={{ fontSize: 30, color: "white" }} /> &nbsp;
                        <p style={divStyle}>Alerts</p>
                    </NavLink>
                </Button>

            </ListItem >
        );
    }
}

export default SidePanelMenu;
