import React from "react";
import { Switch, Route } from "react-router-dom";
import DashBoard from "../components/DashBoard";
import VerticalStepper from "../components/VerticalStepper";
import FormsStepper from "../components/FormsStepper";
import AddUser from "../components/AddUser";
import siteDetails from "../components/siteDetails";
// The Main component renders one of the three provided
// Routes (provided that one matches). Both the /roster
// and /schedule routes will match any pathname that starts
// with /roster or /schedule. The / route will only match
// when the pathname is exactly the string "/"
const Main = () => (
  <main>
    <Switch>
      <Route exact path="/" component={DashBoard} />
      {/* <Route exact path="/profile" component={VerticalStepper} /> */}
      <Route exact path="/profile" component={FormsStepper} />
      <Route exact path="/user" component={AddUser} />
      <Route exact path="/sitedetails" component={siteDetails} />
    </Switch>
  </main>
);

export default Main;
