import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { generatorInfoAction } from "../actions/SurveyActions";

class FormGeneratorInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        GI_No_of_Generators: "",
        GI_Generator_Sl_No: "",
        GI_Gen1_Rating: "",
        GI_Gen2_Rating: "",
        GI_Engine1_Brand: "",
        GI_Engine2_Brand: "",
        GI_Gen1_Running_Hrs: "",
        GI_Gen2_Running_Hrs: "",
        GI_Gen1_Status: "",
        GI_Gen2_Status: "",
        GI_ATS_Exists: "",
        GI_ATS_Status: "",
        GI_ATS_Type: "",
        GI_ATS_AMF_Brand: "",
        GI_Tank_Capacity: "",
        GI_Tank_Shape: "",
        GI_Tank_Length: "",
        GI_Tank_Width: "",
        GI_Tank_Height: "",
        GI_Diesel_level_Height: "",
        GI_Diesel_Quantity: "",
        GI_Water_Separator_Exists: "",
        GI_Site_Job_Card_Exists: "",
      },
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.generatorInfo });
  }

  render() {
    const { generatorInfoDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      generatorInfoDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="No of Generators"
                id="GI_No_of_Generators"
                onChange={onChangeValue}
                value={this.state.formData.GI_No_of_Generators}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Generator Serial No."
                id="GI_Generator_Sl_No"
                onChange={onChangeValue}
                value={this.state.formData.GI_Generator_Sl_No}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Gen-1 Rating (KVA)"
                id="GI_Gen1_Rating"
                onChange={onChangeValue}
                value={this.state.formData.GI_Gen1_Rating}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Gen-2 Rating (KVA)"
                id="GI_Gen2_Rating"
                onChange={onChangeValue}
                value={this.state.formData.GI_Gen2_Rating}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Engine-1 Brand"
                id="GI_Engine1_Brand"
                onChange={onChangeValue}
                value={this.state.formData.GI_Engine1_Brand}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Engine-2 Brand"
                id="GI_Engine2_Brand"
                onChange={onChangeValue}
                value={this.state.formData.GI_Engine2_Brand}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Gen-1 Running Hrs"
                id="GI_Gen1_Running_Hrs"
                onChange={onChangeValue}
                value={this.state.formData.GI_Gen1_Running_Hrs}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Gen-2 Running Hrs"
                id="GI_Gen2_Running_Hrs"
                onChange={onChangeValue}
                value={this.state.formData.GI_Gen2_Running_Hrs}
              />

            </div>
          </Grid>

          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Gen-1 Status "
                id="GI_Gen1_Status"
                onChange={onChangeValue}
                value={this.state.formData.GI_Gen1_Status}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Gen-2 Status"
                id="GI_Gen2_Status"
                onChange={onChangeValue}
                value={this.state.formData.GI_Gen2_Status}
              />
              <TextField
                fullWidth
                className="m-2"
                label="ATS"
                id="GI_ATS_Exists"
                onChange={onChangeValue}
                value={this.state.formData.GI_ATS_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="ATS Status"
                id="GI_ATS_Status"
                onChange={onChangeValue}
                value={this.state.formData.GI_ATS_Status}
              />
              <TextField
                fullWidth
                className="m-2"
                label="ATS Type"
                id="GI_ATS_Type"
                onChange={onChangeValue}
                value={this.state.formData.GI_ATS_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="ATS (AMF) Brand"
                id="GI_ATS_AMF_Brand"
                onChange={onChangeValue}
                value={this.state.formData.GI_ATS_AMF_Brand}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Tank Capacity (L)"
                id="GI_Tank_Capacity"
                onChange={onChangeValue}
                value={this.state.formData.GI_Tank_Capacity}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Tank Shape "
                id="GI_Tank_Shape"
                onChange={onChangeValue}
                value={this.state.formData.GI_Tank_Shape}
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Tank Length (cm)"
                id="GI_Tank_Length"
                onChange={onChangeValue}
                value={this.state.formData.GI_Tank_Length}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Tank Width / Dia (cm)"
                id="GI_Tank_Width"
                onChange={onChangeValue}
                value={this.state.formData.GI_Tank_Width}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Tank Height (cm)"
                id="GI_Tank_Height"
                onChange={onChangeValue}
                value={this.state.formData.GI_Tank_Height}
              />
              <TextField
                fullWidth
                className="m-2"
                label=" Diesel level Height (cm)"
                id="GI_Diesel_level_Height"
                onChange={onChangeValue}
                value={this.state.formData.GI_Diesel_level_Height}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Diesel Quantity (L)"
                id="GI_Diesel_Quantity"
                onChange={onChangeValue}
                value={this.state.formData.GI_Diesel_Quantity}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Water Separator"
                id="GI_Water_Separator_Exists"
                onChange={onChangeValue}
                value={this.state.formData.GI_Water_Separator_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Job Card"
                id="GI_Site_Job_Card_Exists"
                onChange={onChangeValue}
                value={this.state.formData.GI_Site_Job_Card_Exists}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    generatorInfo: state.generatorInfo,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    generatorInfoDispatch: (data) => dispatch(generatorInfoAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormGeneratorInfo);