import React, { Component, Fragment } from "react";
import {TextField,Grid} from "@material-ui/core";

class FormAntennaPosition extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
  }

  render() {
    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tenant-1 2G/3G/4G"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tenant-1 MW"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tenant-2 2G/3G/4G"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tenant-2 MW"
              />
            
              
            </div>
          </Grid>

          <Grid item xs={12} lg={6}>
            <div className="p-3">
            <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tenant-3 2G/3G/4G"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tenant-3 MW"
              />
             
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

export default FormAntennaPosition;

