import React, { Component, Fragment } from 'react';
import '../css/sidebar.css';
import '../css/blog.css';
import { IconButton, LinearProgress, Grid, Card, CardContent, Button, Divider, Tooltip } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { connect } from "react-redux";
import Tables from "../components/Tables";
import WorkOrderTable from "../components/WorkOrderTable";
import ColumnChart from "../components/chart";
import MapChart from "../components/MapChart";


const mapStateToProps = state => {
  return { currentMonth: state.currentMonth };
};



class DashBoardConnect extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    let legend = '';
    return (
      <Fragment className="contentpanel-wrapper">

        <Grid container spacing={4}>
          <Grid item xs={12} sm={6} md={4}>
            <Card className="card-box bg-royal border-0 text-light mb-4">
              <CardContent className="p-3">
                <div className="d-flex align-items-start">
                  <div className="font-weight-bold">
                    <small className="text-white-50 d-block mb-1 text-uppercase">
                      New Orders
                  </small>
                    <span className="font-size-xxl mt-1">10</span>
                  </div>
                  <div className="ml-auto">
                    <div className="bg-white text-center text-success d-50 rounded-circle d-flex align-items-center justify-content-center">
                      <FontAwesomeIcon
                        icon={['far', 'chart-bar']}
                        className="font-size-xl"
                      />
                    </div>
                  </div>
                </div>
                <div className="mt-3">
                  <FontAwesomeIcon
                    icon={['fas', 'arrow-up']}
                    className="text-white mr-1"
                  />
                  <span className="text-white pr-1">27</span>
                  <span className="text-white-50">New sites</span>
                </div>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <Card className="card-box bg-vicious-stance text-light mb-4">
              <CardContent className="p-3">
                <div className="d-flex align-items-start">
                  <div className="font-weight-bold">
                    <small className="text-white-50 d-block mb-1 text-uppercase">
                      Completed Orders
                  </small>
                    <span className="font-size-xxl mt-1">23</span>
                  </div>
                  <div className="ml-auto">
                    <div className="bg-white text-center text-primary d-50 rounded-circle d-flex align-items-center justify-content-center">
                      <FontAwesomeIcon
                        icon={['far', 'chart-bar']}
                        className="font-size-xl"
                      />
                    </div>
                  </div>
                </div>
                <div className="mt-3">
                  <FontAwesomeIcon
                    icon={['fas', 'arrow-up']}
                    className="text-success mr-1"
                  />
                  <span className="text-success pr-1">47</span>
                  <span className="text-white-50">Sites completed</span>
                </div>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={4}>
            <Card className="card-box bg-midnight-bloom text-light mb-4">
              <CardContent className="p-3">
                <div className="d-flex align-items-start">
                  <div className="font-weight-bold">
                    <small className="text-white-50 d-block mb-1 text-uppercase">
                      Pending Orders
                  </small>
                    <span className="font-size-xxl mt-1">7</span>
                  </div>
                  <div className="ml-auto">
                    <div className="bg-white text-center text-primary d-50 rounded-circle d-flex align-items-center justify-content-center">
                      <FontAwesomeIcon
                        icon={['far', 'chart-bar']}
                        className="font-size-xl"
                      />
                    </div>
                  </div>
                </div>
                <div className="mt-3">
                  <FontAwesomeIcon
                    icon={['fas', 'arrow-down']}
                    className="text-warning mr-1"
                  />
                  <span className="text-warning px-1">15</span>
                  <span className="text-white-50">Sites pending</span>
                </div>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
        <Grid container spacing={4}>
          <WorkOrderTable />
        </Grid>
        <Grid container spacing={4}>
        <Grid item xs={12} lg={6} md={6}>
            <ColumnChart />
            </Grid>
            <Grid item xs={12} lg={6} md={6}>
            <MapChart />
            </Grid>
        </Grid>
      </Fragment>

    );
  }
}
const DashBoard = connect(mapStateToProps)(DashBoardConnect);
export default DashBoard;