import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { saqSiteAction } from "../actions/SurveyActions";

class FormSAQSiteType extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        SAQ_Site_Type: "",
        SAQ_Site_Configuration: "",
        SAQ_Site_Status: "",
        SAQ_SAQ_Type: "",
        SAQ_Lease_Period: "",
        SAQ_Lease_Value: "",
        SAQ_Purchase_Price: "",
        SAQ_Start_Date: "",
        SAQ_Years_Paid_In_Advance: "",
        SAQ_Remaining_Years: "",
        SAQ_Escalator: "",
        SAQ_No_Of_Renewable_Years: "",
        SAQ_Acquired_Land_Size: "",
        SAQ_Landlord_Contact: "",
        SAQ_Site_Size: "",
      }
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.saqSite });
  }

  render() {
    const { saqSiteDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      saqSiteDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Site Type"
                id="SAQ_Site_Type"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Site_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Configuration"
                id="SAQ_Site_Configuration"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Site_Configuration}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Status"
                id="SAQ_Site_Status"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Site_Status}
              />
              <TextField
                fullWidth
                className="m-2"
                label="SAQ Type"
                id="SAQ_SAQ_Type"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_SAQ_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Lease Period (yrs)"
                id="SAQ_Lease_Period"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Lease_Period}
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Lease Value (NGN)"
                id="SAQ_Lease_Value"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Lease_Value}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Purchase Price (NGN)"
                id="SAQ_Purchase_Price"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Purchase_Price}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Start Date"
                id="SAQ_Start_Date"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Start_Date}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Years Paid in Advance (yrs)"
                id="SAQ_Years_Paid_In_Advance"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Years_Paid_In_Advance}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Remaining Years (yrs)"
                id="SAQ_Remaining_Years"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Remaining_Years}
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Escalator (%)"
                id="SAQ_Escalator"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Escalator}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of Renewable Years (yrs)"
                id="SAQ_No_Of_Renewable_Years"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_No_Of_Renewable_Years}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Acquired Land Size (m)"
                id="SAQ_Acquired_Land_Size"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Acquired_Land_Size}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Landlord Contact"
                id="SAQ_Landlord_Contact"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Landlord_Contact}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Size (m)"
                id="SAQ_Site_Size"
                onChange={onChangeValue}
                value={this.state.formData.SAQ_Site_Size}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    saqSite: state.saqSite,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    saqSiteDispatch: (data) => dispatch(saqSiteAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormSAQSiteType);
