import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { towerInfoAction } from "../actions/SurveyActions";

class FormTowerInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
      formData: {
        TI_Tower_Height: "",
        TI_Tower_Brand: "",
        TI_Tower_Type: "",
        TI_No_Of_Legs: "",
        TI_Rooftop_Pole_Qty: "",
        TI_Rooftop_Poles_Height: "",
        TI_Monopole: "",
        TI_Tower_Design_Type: "",
        TI_Tower_Capacity: "",
        TI_Age: "",
        TI_Painting_Status: "",
        TI_Condition: "",
        TI_Tower_Capacity_Type: "",
        TI_Tower_Earthing: "",
        TI_RF_Grounding: "",
        TI_Availability_Of_Ladder: "",
        TI_Availability_Of_Working_Platform: "",
        TI_Aviation_Light_Installed: "",
        TI_Lightening_Arrestor_Installed: "",
      },
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.towerInfo });
  }

  render() {
    const { towerInfoDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      towerInfoDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Height"
                label="Tower Height (m)"
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Height}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Brand"
                label="Tower Brand"
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Brand}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Type"
                label="Tower Type"
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_No_Of_Legs"
                label="No of Legs"
                onChange={onChangeValue}
                value={this.state.formData.TI_No_Of_Legs}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Rooftop_Pole_Qty"
                label="Incase of Rooftopp Pole- Qty"
                onChange={onChangeValue}
                value={this.state.formData.TI_Rooftop_Pole_Qty}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Rooftop_Poles_Height"
                label="Incase of Rooftopp Poles(Height)"
                onChange={onChangeValue}
                value={this.state.formData.TI_Rooftop_Poles_Height}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Monopole"
                label="Incase of Monopole"
                onChange={onChangeValue}
                value={this.state.formData.TI_Monopole}
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Design_Type"
                label="Tubular/L-Type"
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Design_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Capacity"
                label="Tower Capacity "
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Capacity}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Age"
                label="Age (yrs)"
                onChange={onChangeValue}
                value={this.state.formData.TI_Age}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Painting_Status"
                label="Painting Status"
                onChange={onChangeValue}
                value={this.state.formData.TI_Painting_Status}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Condition"
                label="Condition"
                onChange={onChangeValue}
                value={this.state.formData.TI_Condition}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Capacity_Type"
                label="Tower Type"
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Capacity_Type}
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="TI_Tower_Earthing"
                label="Tower Earthing "
                onChange={onChangeValue}
                value={this.state.formData.TI_Tower_Earthing}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_RF_Grounding"
                label="RF Grounding"
                onChange={onChangeValue}
                value={this.state.formData.TI_RF_Grounding}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Availability_Of_Ladder"
                label="Availability of Ladder"
                onChange={onChangeValue}
                value={this.state.formData.TI_Availability_Of_Ladder}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Availability_Of_Working_Platform"
                label="Availability of working/resting platform"
                onChange={onChangeValue}
                value={this.state.formData.TI_Availability_Of_Working_Platform}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Aviation_Light_Installed"
                label="Aviation Light"
                onChange={onChangeValue}
                value={this.state.formData.TI_Aviation_Light_Installed}
              />
              <TextField
                fullWidth
                className="m-2"
                id="TI_Lightening_Arrestor_Installed"
                label="Lightening Arrestor"
                onChange={onChangeValue}
                value={this.state.formData.TI_Lightening_Arrestor_Installed}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    towerInfo: state.towerInfo,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    towerInfoDispatch: (data) => dispatch(towerInfoAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormTowerInfo);