import React, { Component, Fragment }from "react";
import Grid from "@material-ui/core/Grid";

import {
    ComposableMap,
    Geographies,
    Geography,
    ZoomableGroup,
    Marker
} from "react-simple-maps";

const geoUrl =
    "https://raw.githubusercontent.com/samateja/D3topoJson/master/nigeria.json";


const markers = [

    { name: "Lagos", coordinates: [3.394673, 6.454066] },
    { name: "Abuja", coordinates: [7.533333, 9.083333] },
    { name: "Kano", coordinates: [8.51316, 12.002381] },
    { name: "Port Harcourt", coordinates: [7.013404, 4.777423] },
    { name: "Benin City", coordinates: [5.625749, 6.338153] },
    { name: "Ogbomoso", coordinates: [4.240144, 8.133725] },
    { name: "Ibadan", coordinates: [3.905907, 7.377562] },
    { name: "Zaria", coordinates: [7.710359, 11.078945] },
    { name: "Gusau", coordinates: [6.664116, 12.17024] },
    { name: "Lafia", coordinates: [8.515316, 8.493897] },
    { name: "Buzava", coordinates: [13.684518, 11.545362] }
];

class MapChart extends Component
{
    constructor(props) {
      super(props);

      };
        render()
            {
                return (
                    <Fragment>
                         <Grid>

                            <ComposableMap
                                projection="geoAzimuthalEqualArea"
                                projectionConfig={{
                                    rotate: [-10, -8, 0],
                                    scale: 2500
                                 }}
                                 width={800}
                                 height={600}
                             >
                             <ZoomableGroup zoom={1}>
                             <Geographies geography={geoUrl}>
                                {({ geographies }) =>
                                    geographies.map(geo => (
                                        <Geography key={geo.rsmKey} geography={geo}
                                            fill="#5383ff"
                                            stroke="#D6D6DA"
                                        />
                                    ))
                                }
                            </Geographies>
                            {markers.map(({ name, coordinates, markerOffset }) => (
                            <Marker key={name} coordinates={coordinates}>
                            <circle r={10} fill="#F00" stroke="#fff" strokeWidth={2} />
                            <text
                                textAnchor="middle"
                                y={markerOffset}
                                style={{ fontFamily: "system-ui", fill: "#5D5A6D" }}
                            >
                                {name}
                            </text>
                        </Marker>
                    ))}
                </ZoomableGroup>
            </ComposableMap>
        </Grid>
        </Fragment>
    );
};
}

export default MapChart;