import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { equipmentInfoAction } from "../actions/SurveyActions";

class FormEquipInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        EIP_Site_Configuration: "",
        EIP_Shelter_Exists: "",
        EIP_Shelter_Dimensions: "",
        EIP_Enough_Indoor_Space: "",
        EIP_Cooling_Topology: "",
        EIP_No_of_ACs: "",
        EIP_AC_Capacity: "",
        EIP_AC_Brand: "",
        EIP_Rectifier_Type: "",
        EIP_Rectifier_Sl: "",
        EIP_Rectifier_Rating: "",
        EIP_System_Voltage: "",
        EIP_No_of_Modules: "",
        EIP_No_of_Empty_Slots: "",
        EIP_Module_Rating: "",
        EIP_Module: "",
        EIP_No_of_CBs_MCBs: "",
        EIP_No_of_Battery_Strings: "",
        EIP_Battery_Type: "",
        EIP_Batteries_Brand: "",
        EIP_No_of_Blocks_per_String: "",
        EIP_Battery_Voltage: "",
        EIP_Battery_Rating: "",
        EIP_Batteries_Status: "",
        EIP_Date_of_Manufacturing_of_Battery: "",
        EIP_ACDB: "",
        EIP_No_of_ACDBs: "",
        EIP_DCDB: "",
        EIP_No_of_DCDBs: "",
        EIP_Surge_Arrestor: "",
        EIP_Monitoring_System: "",
        EIP_Method_of_Reporting: "",
        EIP_AWL: "",
        EIP_AWL_Type: "",
      }
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.equipmentInfo });
  }

  render() {

    const { equipmentInfoDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      equipmentInfoDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Site Configuration"
                id="EIP_Site_Configuration"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Site_Configuration}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Shelter"
                id="EIP_Shelter_Exists"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Shelter_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Shelter Dimensions (m)"
                id="EIP_Shelter_Dimensions"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Shelter_Dimensions}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Enough Indoor Space"
                id="EIP_Enough_Indoor_Space"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Enough_Indoor_Space}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Cooling Topology"
                id="EIP_Cooling_Topology"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Cooling_Topology}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of ACs"
                id="EIP_No_of_ACs"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_ACs}
              />
              <TextField
                fullWidth
                className="m-2"
                label="AC Capacity (BTU)"
                id="EIP_AC_Capacity"
                onChange={onChangeValue}
                value={this.state.formData.EIP_AC_Capacity}
              />
              <TextField
                fullWidth
                className="m-2"
                label="AC Brand"
                id="EIP_AC_Brand"
                onChange={onChangeValue}
                value={this.state.formData.EIP_AC_Brand}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Rectifier Type "
                id="EIP_Rectifier_Type"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Rectifier_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Rectifier Sl No"
                id="EIP_Rectifier_Sl"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Rectifier_Sl}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Rectifier Rating (KW)"
                id="EIP_Rectifier_Rating"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Rectifier_Rating}
              />
              <TextField
                fullWidth
                className="m-2"
                label="System Voltage (VDC)"
                id="EIP_System_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.EIP_System_Voltage}
              />
            </div>
          </Grid>

          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="No of Modules"
                id="EIP_No_of_Modules"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_Modules}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of Empty Slots"
                id="EIP_No_of_Empty_Slots"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_Empty_Slots}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Module Rating (KW)"
                id="EIP_Module_Rating"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Module_Rating}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Module S/L No"
                id="EIP_Module"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Module}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of CBs/MCBs with Ratings"
                id="EIP_No_of_CBs_MCBs"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_CBs_MCBs}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of Battery Strings"
                id="EIP_No_of_Battery_Strings"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_Battery_Strings}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Battery Type"
                id="EIP_Battery_Type"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Battery_Type}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Batteries Brand"
                id="EIP_Batteries_Brand"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Batteries_Brand}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of Blocks per String "
                id="EIP_No_of_Blocks_per_String"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_Blocks_per_String}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Battery Voltage (V / unit)"
                id="EIP_Battery_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Battery_Voltage}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Battery Rating (AH / unit)"
                id="EIP_Battery_Rating"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Battery_Rating}
              />
            </div>
          </Grid>

          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Batteries Status"
                id="EIP_Batteries_Status"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Batteries_Status}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Date of Manufacturing of Battery"
                id="EIP_Date_of_Manufacturing_of_Battery"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Date_of_Manufacturing_of_Battery}
              />
              <TextField
                fullWidth
                className="m-2"
                label="ACDB"
                id="EIP_ACDB"
                onChange={onChangeValue}
                value={this.state.formData.EIP_ACDB}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of ACDBs"
                id="EIP_No_of_ACDBs"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_ACDBs}
              />
              <TextField
                fullWidth
                className="m-2"
                label="DCDB"
                id="EIP_DCDB"
                onChange={onChangeValue}
                value={this.state.formData.EIP_DCDB}
              />
              <TextField
                fullWidth
                className="m-2"
                label="No of DCDBs"
                id="EIP_No_of_DCDBs"
                onChange={onChangeValue}
                value={this.state.formData.EIP_No_of_DCDBs}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Surge Arrestor"
                id="EIP_Surge_Arrestor"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Surge_Arrestor}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Monitoring System"
                id="EIP_Monitoring_System"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Monitoring_System}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Method of Reporting"
                id="EIP_Method_of_Reporting"
                onChange={onChangeValue}
                value={this.state.formData.EIP_Method_of_Reporting}
              />
              <TextField
                fullWidth
                className="m-2"
                label="AWL"
                id="EIP_AWL"
                onChange={onChangeValue}
                value={this.state.formData.EIP_AWL}
              />
              <TextField
                fullWidth
                className="m-2"
                label="AWL Type"
                id="EIP_AWL_Type"
                onChange={onChangeValue}
                value={this.state.formData.EIP_AWL_Type}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    equipmentInfo: state.equipmentInfo,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    equipmentInfoDispatch: (data) => dispatch(equipmentInfoAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormEquipInfo);