import React, { Component, Fragment } from "react";
import clsx from "clsx";
import { Link } from 'react-router-dom';
import {
  Hidden,
  IconButton,
  AppBar,
  Box,
  Button,
  Tooltip,
} from "@material-ui/core";
import Oreneta_logo from '../img/Oreneta_logo.png';
import MenuOpenRoundedIcon from "@material-ui/icons/MenuOpenRounded";
import MenuRoundedIcon from "@material-ui/icons/MenuRounded"
import { Grid } from '@material-ui/core';

import HeaderUserbox from '../components/HeaderUserbox';


class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarToggleMobile: true,
      headerFixed: true,
      headerShadow: true,
      isCollapsedLayout: true
    };
    this.mobileSidebarToggle = this.mobileSidebarToggle.bind(this);
  }
  toggleSidebarMobile = () => {
    this.setState({
      sidebarToggleMobile: !this.state.sidebarToggleMobile,
    });
  };
  mobileSidebarToggle(e) {
    e.preventDefault();
    document.documentElement.classList.toggle("nav-open");
  }
  render() {
    return (
      <Fragment>
        <Grid container>
          <Grid item xs={12} sm={12} md={12}>
            <AppBar
              color="primary"
              className={clsx("app-header", {})}
              position={this.state.headerFixed ? "fixed" : "absolute"}
              elevation={this.state.headerShadow ? 11 : 3}
            >
              {!this.isCollapsedLayout}
              <Box className="bg-vicious-stance app-header-toolbar">
                <Hidden lgUp>
                  <Box
                    className="app-logo-wrapper"
                    title="Carolina React Admin Dashboard with Material-UI Free">
                    <Link to="/DashboardDefault" className="app-logo-link">
                      <IconButton
                        color="primary"
                        size="medium"
                        className="app-logo-btn" >
                        <img
                          className="app-logo-img"
                          alt="Carolina React Admin Dashboard with Material-UI Free"
                          src={Oreneta_logo}
                        />
                      </IconButton>
                    </Link>

                    <Hidden smDown>
                      <Box className="app-logo-text">7Miracles</Box>
                    </Hidden>
                  </Box>
                </Hidden>
                <Hidden mdDown>
                  <Box className="d-flex align-items-center" />
                </Hidden>
                <Box className="d-flex align-items-center">
                  <HeaderUserbox />
                  <Box className="toggle-sidebar-btn-mobile">
                    <Tooltip title="Toggle Sidebar" placement="right">
                      <IconButton
                        color="inherit"
                        onClick={this.toggleSidebarMobile}
                        size="medium"
                      >
                        {this.state.sidebarToggleMobile ? (
                          <MenuOpenRoundedIcon />
                        ) : (
                            <MenuRoundedIcon />
                          )}
                      </IconButton>
                    </Tooltip>
                  </Box>
                </Box>
              </Box>
            </AppBar>
          </Grid></Grid>
      </Fragment>
    );
  }
}

export default Header;
