import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { gridPowerInfoAction } from "../actions/SurveyActions";

class FormGridPowerInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        GPI_Authority_Power: "",
        GPI_Consumer_number: "",
        GPI_Transformer_Exists: "",
        GPI_Transformer_Rate: "",
        GPI_Transformer_Voltage: "",
        GPI_AVR_Exists: "",
        GPI_AVR_Rate: "",
        GPI_Hrs_Available_Day: "",
        GPI_Average_Tariff: "",
        GPI_Power_Meter_Exists: "",
        GPI_Meter_Serial: "",
        GPI_Power_Poles_Mounted: "",
        GPI_Power_Fuses: "",
      }
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.gridPowerInfo });
  }

  render() {
    const { gridPowerInfoDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      gridPowerInfoDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Authority Power"
                id="GPI_Authority_Power"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Authority_Power}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Consumer number"
                id="GPI_Consumer_number"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Consumer_number}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Transformer"
                id="GPI_Transformer_Exists"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Transformer_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Transformer Rate (KVA)"
                id="GPI_Transformer_Rate"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Transformer_Rate}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Transformer Voltage (KV)"
                id="GPI_Transformer_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Transformer_Voltage}
              />
            </div>
          </Grid>

          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="AVR"
                id="GPI_AVR_Exists"
                onChange={onChangeValue}
                value={this.state.formData.GPI_AVR_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="AVR Rate (KVA)"
                id="GPI_AVR_Rate"
                onChange={onChangeValue}
                value={this.state.formData.GPI_AVR_Rate}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Hrs Available/Day (hrs)"
                id="GPI_Hrs_Available_Day"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Hrs_Available_Day}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Average Tariff (NGN)"
                id="GPI_Average_Tariff"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Average_Tariff}
              />

            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Power Meter"
                id="GPI_Power_Meter_Exists"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Power_Meter_Exists}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Meter Serial No"
                id="GPI_Meter_Serial"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Meter_Serial}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Power Poles Mounted"
                id="GPI_Power_Poles_Mounted"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Power_Poles_Mounted}
              />
              <TextField
                fullWidth
                className="m-2"
                label="Power Fuses"
                id="GPI_Power_Fuses"
                onChange={onChangeValue}
                value={this.state.formData.GPI_Power_Fuses}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    gridPowerInfo: state.gridPowerInfo,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    gridPowerInfoDispatch: (data) => dispatch(gridPowerInfoAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormGridPowerInfo);