function getSteps() {
  return ["Site Identification", "SAQ and site Type", "Tower Information", "Civil Information",
    " Generator Information", "Grid Power Information", "Equipment Information", "Power Readings",
    "Telecom Equipment Information", "General Information", "Environmental Information", "Antenna Position", "External Alarms"];
}


