import React, { Component, Fragment } from "react";

class ProgressBar extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Fragment>
                <div>
                    <div className="pr-0 progress">
                        <div
                            className="progress-bar bg-success"
                            role="progressbar"
                            aria-valuenow={this.props.progressnow}
                            aria-valuemin="0"
                            aria-valuemax="100"
                            style={{ width: this.props.progressnow }}
                        >
                            {this.props.progressnow}
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default ProgressBar;