import React, { Component, Fragment } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { withStyles } from '@material-ui/core/styles';
//import UploadIcon from "../components/UploadIcon";
//import VerifyTable from "../components/VerifyTable";
import ExcelUpload from "../components/ExcelUpload";
import { connect } from "react-redux";
import '../Excel/excelupload.css';
import { OutTable, ExcelRenderer } from 'react-excel-renderer';
import { Col, InputGroup, InputGroupAddon, FormGroup, Label, Fade, FormFeedback, Container, Card } from 'reactstrap';

function getSteps() {
  return ['Upload Files', 'Verify and Confirm', 'Create Work Order'];
}


function getStepContent(step) {
  switch (step) {
    case 0:
      return `Upload your file here`;
    case 1:
      return 'Verify the document and click Next to confirm';
    case 2:
      return `Submit Service Order`;
    default:
      return 'Unknown step';
  }
}
const steps = getSteps();
const useStyles = theme => ({
  root: {
    width: '100%',
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
});

const mapStateToProps = state => {
  return { excelPreview: state.excelPreview };
};


class VerticalStepperConnect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0
    };
  };

  handleNext = () => {
    this.setState({
      activeStep: this.state.activeStep + 1
    });
  };
  handleBack = () => {
    this.setState({
      activeStep: this.state.activeStep - 1
    });
  };
  handleReset = () => {
    this.setState({
      activeStep: 0
    });
  };

  render() {
    const { classes } = this.props;
    return (
      <div >
        <Stepper activeStep={this.state.activeStep} orientation="vertical">
          <Step key={"Upload Files"}>
            <StepLabel>{"Upload Files"}</StepLabel>
            <StepContent>
              <Typography>{"Upload your file here"}
                <ExcelUpload />
              </Typography>
              <div className={classes.actionsContainer}>

                <div>
                  <Button
                    disabled={this.state.activeStep === 0}
                    onClick={this.handleBack}
                    className={classes.button}
                  >
                    Back
                    </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={this.handleNext}
                    className={classes.button}
                  >
                    {this.state.activeStep === steps.length - 1 ? "Finish" : "Next"}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
          {/*STEP 2 */}
          <Step key={"Verify the service order and click Next to confirm"}>
            <StepLabel>{"Verify the service order and click Next to confirm"}</StepLabel>
            <StepContent>
              <Typography>{"Verify the service order and click Next to confirm"}
              </Typography>
              <div className={classes.actionsContainer}>
                {this.state.activeStep === 1 && <div>
                  <Card body outline color="secondary" className="restrict-card">
                    <OutTable data={this.props.excelPreview.rows} columns={this.props.excelPreview.cols} tableClassName="ExcelTable2007" tableHeaderRowClass="heading" />
                  </Card>
                </div>}
                <div>
                  <Button
                    disabled={this.state.activeStep === 0}
                    onClick={this.handleBack}
                    className={classes.button}
                  >
                    Back
                    </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={this.handleNext}
                    className={classes.button}
                  >
                    {this.state.activeStep === steps.length - 1 ? "Finish" : "Next"}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
          {/* STEP 3 */}
          <Step key={"Approve Service Order"}>
            <StepLabel>{"Approve Service Order"}</StepLabel>
            <StepContent>
              <Typography>{"Click next to approve service order"}
              </Typography>
              <div className={classes.actionsContainer}>

                <div>
                  <Button
                    disabled={this.state.activeStep === 0}
                    onClick={this.handleBack}
                    className={classes.button}
                  >
                    Back
                    </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={this.handleNext}
                    className={classes.button}
                  >
                    {this.state.activeStep === steps.length - 1 ? "Finish" : "Next"}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>

        </Stepper>
        {this.state.activeStep === steps.length && (
          <Paper square elevation={0} className={classes.resetContainer}>
            <Typography>All steps completed - you&apos;re finished</Typography>
            <Button onClick={this.handleReset} className={classes.button}>
              Reset
            </Button>
          </Paper>
        )}
      </div>
    );
  }
}
const VerticalStepper = connect(mapStateToProps)(VerticalStepperConnect);
export default withStyles(useStyles)(VerticalStepper);
