import axios from 'axios';

console.log("MODE : ", process.env.NODE_ENV);
const domain = process.env.NODE_ENV == 'development' ? 'http://localhost:3000' : 'http://intelligentspm.com:3000';

const getToken = () => {
    axios.defaults.headers.common['Authorization'] = sessionStorage.getItem("authToken") || null;
}

const setToken = (token) => {
    sessionStorage.setItem("authToken", token);
}

const GET = (url) => {
    getToken();
    return axios.get(domain + url);
}

const POST = (url, payload) => {
    getToken();
    return axios.post(domain + url, payload);
}

const DELETE = (url) => {
    getToken();
    return axios.delete(domain + url);
}

export { GET, POST, DELETE };