import React, { Component, Fragment } from "react";
import {
    IconButton,
    LinearProgress,
    Grid,
    Card,
    CardContent,
    Button,
    Divider,
    Tooltip,
} from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { connect } from "react-redux";
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import PropTypes from "prop-types";
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Collapse from "@material-ui/core/Collapse";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
import RefreshIcon from "@material-ui/icons/Refresh";
import CollapsableTable from "../components/CollapsableTable";
import ProgressBar from "../components/progressBar";
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
const rows = require('../config/wodetails.json');

const mapStateToProps = (state) => {
    return {};
};

const useRowStyles = makeStyles({
    root: {
        "& > *": {
            borderBottom: "unset",
        },
    },
});

class WorkOrderTable extends Component {
    constructor(props) {
        super(props);

        let obj = [];
        rows.map((row, index) => {
            obj[index] = row.assignee;
        });
        this.state = {
            open: false,
            assignee: obj,
            displaynone: true,
            displayAssignee: true
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleOnClick = this.handleOnClick.bind(this);
    }
    setOpen = () => {
        this.setState({
            open: !this.state.open,
        });
    };
    handleOnClick = () => {
        this.setState({
            displaynone: false,
            displayAssignee: false
        });
    };
    handleChange = index => (event) => {
        let assignee = [...this.state.assignee];
        assignee[index] = event.target.value;
        this.setState({ assignee });
        this.setState({
            displaynone: true,
            displayAssignee: true
        });
        // this.setState({
        //     assignee: event.target.value,
        // });
    };

    render() {
        return (
            <Fragment>
                <Grid item xs={12} lg={12}>
                    <Card className="card-box mb-4">
                        <div className="card-header pr-2">
                            <div className="card-header--title">New Orders</div>
                            <div className="card-header--actions">
                                <Tooltip arrow title="Refresh">
                                    <IconButton size="small" color="primary" className="mr-3">
                                        <FontAwesomeIcon icon={["fas", "cog"]} spin />
                                    </IconButton>
                                </Tooltip>
                            </div>
                        </div>
                        <CardContent className="p-3">
                            <TableContainer className="table-responsive">
                                <Table className="table table-borderless table-hover text-nowrap mb-0">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell />
                                            <TableCell className="text-left">Work Orders</TableCell>
                                            <TableCell className="text-center">Status</TableCell>
                                            <TableCell className="text-center">Progress</TableCell>
                                            <TableCell className="text-center">Assignee</TableCell>
                                            <TableCell className="text-center">Createddate</TableCell>
                                            <TableCell className="text-center">noofsites</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>

                                        {rows.map((row, index) => (<>
                                            <TableRow>
                                                <TableCell>
                                                    <IconButton
                                                        aria-label="expand row"
                                                        size="small"
                                                        onClick={this.setOpen}
                                                    >
                                                        {this.state.open ? (
                                                            <KeyboardArrowUpIcon />
                                                        ) : (
                                                                <KeyboardArrowDownIcon />
                                                            )}
                                                    </IconButton>
                                                </TableCell>
                                                <TableCell>
                                                    <div>
                                                        <a
                                                            href="/profile"

                                                            className="font-weight-bold text-black"
                                                            title="..."
                                                        >
                                                            {row.workorder}
                                                        </a>
                                                        <span className="text-black-50 d-block">
                                                            {row.timetocomplete}
                                                        </span>
                                                    </div>
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <div className={
                                                        row.status === 'pending' ? "badge badge-warning px-4" : row.status === 'completed' ?
                                                            "badge badge-success px-4" : "badge badge-danger px-4"}>{row.status}</div>
                                                </TableCell>
                                                <TableCell>
                                                    <ProgressBar progressnow={row.progressnow} />
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <div className="font-weight-normal  text-black ">

                                                        <span className={this.state.displaynone ? "display-none" : ""}>
                                                            <Select
                                                                labelId="demo-simple-select-label"
                                                                id="demo-simple-select"
                                                                value={this.state.assignee[index]}
                                                                onChange={this.handleChange(index)}
                                                            >
                                                                <MenuItem value={'John Michael'}>John Michael</MenuItem>
                                                                <MenuItem value={'Morkel Styne'}>Morkel Styne</MenuItem>
                                                                <MenuItem value={'Mike David'}>Mike David</MenuItem>
                                                                <MenuItem value={'Mike Swain'}>Mike Swain</MenuItem>
                                                            </Select>
                                                        </span>
                                                        <span className={this.state.displayAssignee ? "font-weight-normal  text-black" : "display-none"}
                                                        >
                                                            {this.state.assignee[index]}
                                                        </span>

                                                        <span class="material-icons MuiIcon-root icon-size" aria-hidden="true" onClick={this.handleOnClick}>edit</span>
                                                    </div>
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <div className="font-weight-normal  text-black">
                                                        {row.createddate}
                                                    </div>
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <div className="font-weight-normal  text-black">
                                                        {row.noofsites}
                                                    </div>
                                                </TableCell>

                                            </TableRow>
                                            <TableRow>
                                                <td
                                                    style={{ paddingBottom: 0, paddingTop: 0 }}
                                                    colSpan={6}
                                                >
                                                    <Collapse
                                                        in={this.state.open}
                                                        timeout="auto"
                                                        unmountOnExit
                                                    >
                                                        <CollapsableTable
                                                            serviceAssignmentRows={row.ServiceAssignment} />
                                                    </Collapse>
                                                </td>
                                            </TableRow>
                                        </>
                                        ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </CardContent>
                    </Card>
                </Grid>
            </Fragment>
        );
    }
}
//const Tables = connect(mapStateToProps)(TablesConnect);
export default WorkOrderTable;
