import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { powerReadingInfoAction } from "../actions/SurveyActions";

class FormPowerReading extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        PR_PH1_AC_Current: "",
        PR_PH1_AC_Voltage: "",
        PR_PH2_AC_Current: "",
        PR_PH2_AC_Voltage: "",
        PR_PH3_AC_Current: "",
        PR_PH3_AC_Voltage: "",
        PR_DC_Rectifier_Current: "",
        PR_DC_Rectifier_Voltage: "",
      }
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.powerReading });
  }

  render() {
    const { powerReadingDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      powerReadingDispatch(this.state.formData);
    }

    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="PH-1 AC Current (A)"
                id="PR_PH1_AC_Current"
                onChange={onChangeValue}
                value={this.state.formData.PR_PH1_AC_Current}
              />
              <TextField
                fullWidth
                className="m-2"
                label="PH-1 AC Voltage (V)"
                id="PR_PH1_AC_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.PR_PH1_AC_Voltage}
              />
              <TextField
                fullWidth
                className="m-2"
                label="PH-2 AC Current (A)"
                id="PR_PH2_AC_Current"
                onChange={onChangeValue}
                value={this.state.formData.PR_PH2_AC_Current}
              />
            </div>
          </Grid>

          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="PH-2 AC Voltage (V)"
                id="PR_PH2_AC_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.PR_PH2_AC_Voltage}
              />
              <TextField
                fullWidth
                className="m-2"
                label="PH-3 AC Current (A)"
                id="PR_PH3_AC_Current"
                onChange={onChangeValue}
                value={this.state.formData.PR_PH3_AC_Current}
              />
              <TextField
                fullWidth
                className="m-2"
                label="PH-3 AC Voltage (V)"
                id="PR_PH3_AC_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.PR_PH3_AC_Voltage}
              />
            </div>
          </Grid>

          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="DC Rectifier Current (A)"
                id="PR_DC_Rectifier_Current"
                onChange={onChangeValue}
                value={this.state.formData.PR_DC_Rectifier_Current}
              />
              <TextField
                fullWidth
                className="m-2"
                label="DC Rectifier Voltage (V)"
                id="PR_DC_Rectifier_Voltage"
                onChange={onChangeValue}
                value={this.state.formData.PR_DC_Rectifier_Voltage}
              />
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    powerReading: state.powerReading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    powerReadingDispatch: (data) => dispatch(powerReadingInfoAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormPowerReading);