import React, { Component, Fragment } from "react";
import { TextField, Grid } from "@material-ui/core";

class FormTelecomEquip extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
  }

  render() {
    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Outdoor or Indoor"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="No. of Tenanants"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="No of BTS"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="BTS Brand"
              />
             
            </div>
          </Grid>

          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="No of TRX"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="TX Brand"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="No of Feeders Mounted"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="MW Links"
              />
             
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

export default FormTelecomEquip;
