import React, { Component, Fragment } from "react";
import {TextField,Grid} from "@material-ui/core";

class FormEnvironInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
  }

  render() {
    return (
      <Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Site Waste Management"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Fire Fighting System"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Leakages"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Any Obsolete Equipment"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Tank Indicator Hose"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Secondary Containment"
              />
              
            </div>
          </Grid>

          <Grid item xs={12} lg={6}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Any Tenant is Present"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Noise Level from Generators"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Setback for Tower"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Offset to Residential Area (m)"
              />
              <TextField
                fullWidth
                className="m-2"
                id="standard-basic"
                label="Visible Land Contamination"
              />
             
            </div>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

export default FormEnvironInfo;

