import {
    FORM_SITE_IDENTIFICATION,
    FORM_SAQ_SITE,
    FORM_TOWER_INFO,
    FORM_CIVIL_INFO,
    FORM_GENERATOR_INFO,
    FORM_GRID_POWER_INFO,
    FORM_EQUIPMENT_INFO,
    FORM_POWER_READING,
} from '../constants/action-types';

function siteIdentificationAction(data) {
    return { type: FORM_SITE_IDENTIFICATION, payload: data };
}

function saqSiteAction(data) {
    return { type: FORM_SAQ_SITE, payload: data };
}

function towerInfoAction(data) {
    return { type: FORM_TOWER_INFO, payload: data };
}

function civilInfoAction(data) {
    return { type: FORM_CIVIL_INFO, payload: data };
}

function generatorInfoAction(data) {
    return { type: FORM_GENERATOR_INFO, payload: data };
}

function gridPowerInfoAction(data) {
    return { type: FORM_GRID_POWER_INFO, payload: data };
}

function equipmentInfoAction(data) {
    return { type: FORM_EQUIPMENT_INFO, payload: data };
}

function powerReadingInfoAction(data) {
    return { type: FORM_POWER_READING, payload: data };
}

export {
    siteIdentificationAction,
    saqSiteAction,
    towerInfoAction,
    civilInfoAction,
    generatorInfoAction,
    gridPowerInfoAction,
    equipmentInfoAction,
    powerReadingInfoAction,
}