import React, { Component } from 'react';
import '../Excel/excelupload.css';
import '../Excel/package.json';
import '../Excel/package-lock.json';
import '../Excel/excel-2007-header-bg.gif'
import '../Excel/excel-2007-header-left.gif'
import { connect } from "react-redux";

import { OutTable, ExcelRenderer } from 'react-excel-renderer';
import Button from "@material-ui/core/Button";
import Input from "@material-ui/core/Input";
import { Col, InputGroup, InputGroupAddon, FormGroup, Label, Fade, FormFeedback, Container, Card } from 'reactstrap';
import updateExcel from '../actions/action';

const mapStateToProps = state => {
  return { excelData: state.excelData };
};
function mapDispatchToProps(dispatch) {
  return {
    updateExcel: expenseList => dispatch(updateExcel(expenseList))
  };
}
class ExcelUploadConnect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      dataLoaded: false,
      isFormInvalid: false,
      rows: null,
      cols: null
    }
    this.fileHandler = this.fileHandler.bind(this);
    this.toggle = this.toggle.bind(this);
    this.openFileBrowser = this.openFileBrowser.bind(this);
    this.renderFile = this.renderFile.bind(this);
    this.updateStore = this.updateStore.bind(this);
    this.openNewPage = this.openNewPage.bind(this);
    this.fileInput = React.createRef();
  }

  renderFile = (fileObj) => {
    //just pass the fileObj as parameter
    ExcelRenderer(fileObj, (err, resp) => {
      if (err) {
        console.log(err);
      }
      else {
        this.setState({
          dataLoaded: true,
          cols: resp.cols,
          rows: resp.rows
        });
        this.updateStore();
      }
    });
  }
  updateStore = () => {
    var data = {
      rows: this.state.rows,
      cols: this.state.cols
    };
    this.props.updateExcel(data);
  }
  fileHandler = (event) => {
    if (event.target.files.length) {
      let fileObj = event.target.files[0];
      let fileName = fileObj.name;


      //check for file extension and pass only if it is .xlsx and display error message otherwise
      if (fileName.slice(fileName.lastIndexOf('.') + 1) === "xlsx") {
        this.setState({
          uploadedFileName: fileName,
          isFormInvalid: false
        });
        this.renderFile(fileObj)
      }
      else {
        this.setState({
          isFormInvalid: true,
          uploadedFileName: ""
        })
      }
    }
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  openFileBrowser = () => {
    this.fileInput.current.click();
  }

  openNewPage = (chosenItem) => {
    const url = chosenItem === "github" ? "https://github.com/ashishd751/react-excel-renderer" : "https://medium.com/@ashishd751/render-and-display-excel-sheets-on-webpage-using-react-js-af785a5db6a7";
    window.open(url, '_blank');
  }

  render() {

    return (
      <div>

        <Container >
          <form>
            <FormGroup row>
              <Label for="exampleFile" xs={6} sm={4} lg={2} size="lg"></Label>
              <Col xs={4} sm={8} lg={10}>
                <InputGroup>
                  <InputGroupAddon addonType="prepend" style={{ 'display': "inline-block" }}>
                    <Button color="info" style={{ color: "white", 'background-color': "#303f9f" }} onClick={this.openFileBrowser.bind(this)}><i className="cui-file"></i> Browse</Button>
                    &nbsp; &nbsp; &nbsp;
                  <input type="file" hidden onChange={this.fileHandler.bind(this)} ref={this.fileInput} onClick={(event) => { event.target.value = null }} style={{ "height": "30px !important" }} />
                  </InputGroupAddon>
                  <Input type="text" className="form-control" value={this.state.uploadedFileName} readOnly invalid={this.state.isFormInvalid} />
                  <FormFeedback>
                    <Fade in={this.state.isFormInvalid} tag="h6" style={{ fontStyle: "italic" }}>
                      Please select a .xlsx file only !
                  </Fade>
                  </FormFeedback>
                </InputGroup>
              </Col>
            </FormGroup>
          </form>

          {/*this.state.dataLoaded &&

            <div>
              <Card body outline color="secondary" className="restrict-card">
                <OutTable data={this.state.rows} columns={this.state.cols} tableClassName="ExcelTable2007" tableHeaderRowClass="heading" />

              </Card>
            </div>*/}
        </Container>
      </div>
    );
  }
}
const ExcelUpload = connect(null, mapDispatchToProps)(ExcelUploadConnect);
export default ExcelUpload;