import React, { Component, Fragment } from "react";
import { ListItem, Button } from '@material-ui/core';
import { NavLink } from "react-router-dom";
import SidePanelHeader from "../components/SidePanelHeader";
import clsx from "clsx";
import SidePanelMenu from "../components/SidePanelMenu";

import PerfectScrollbar from "react-perfect-scrollbar";
import { Hidden, Drawer, Paper } from "@material-ui/core";
//import '../css/icon.css';
const sidebarMenuContent = (

  <ListItem >
    <Button
      color="primary"
      className={clsx('app-sidebar-button')}
      style={{ marginRight: 'em' }}>
      <NavLink to="/">
        <p>Dashboard</p>
      </NavLink>

    </Button>

  </ListItem>
);
class SidePanel extends Component {
  constructor(props) {
    super(props);
    this.sidebarShadow = true;
    this.sidebarToggleMobile = true;
    this.sidebarFixed = true;
  }
  sidebarToggleMobile = () => { };
  closeDrawer = () => { };
  render() {
    return (
      <Fragment>
        <Hidden mdDown>
          <Paper
            className={clsx("app-sidebar-wrapper", {
              "app-sidebar-wrapper-fixed": this.sidebarFixed,
            })}
            square
            elevation={this.sidebarShadow ? 11 : 3}
          >
            <SidePanelHeader />
            <div
              className={clsx({
                "app-sidebar-menu": this.sidebarFixed,
              })}
            >
              <PerfectScrollbar>
                <SidePanelMenu />
              </PerfectScrollbar>
            </div>
          </Paper>
        </Hidden>
      </Fragment>
    );
  }
}

export default SidePanel;
